@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.5s;
  font-family: 'Roboto', sans-serif;
}

body {
  background: #1a1a1a;
  height: 100%;
}



.movie-card{
border: 5px solid black outset;
display: flex;
flex-direction: column;
grid-gap: 8px;
gap: 8px;
justify-content: space-between;
border-radius: 5px;

background: black;
}

.movie-card h3{
    color: white;
    font-size: 16px;
    font-weight: 400;
    padding: 0 10px;
  
}

.movie-poster{
width: 100%;
position: relative;
border-radius: 5px;
}

.movie-poster img{
    width: 100%;
    border-radius: 5px;
}

.vote-average{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: yellow;
    position: absolute;
    right: 0;
    bottom: 0;
    color: black;
}

.vote-average p{
    font-weight: bold;
    font-size: 14px;   
}

.stars{
    display: flex;
    align-items: center;
    grid-gap: 3px;
    gap: 3px;
    padding: 0 10px;
}

.stars .fa-solid {
    color: gold;
    font-size: 12px;
}

.movie-links{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    margin-bottom: 20px;
    margin-top: 5px;
}

.movie-info,
.watch-btn{
    text-decoration: none;
    color: white;
    border: 1px solid white;
    font-size: 15px;
    font-weight: 400;
    padding: 2px 10px;
    border-radius: 5px;
}

.watch-btn{
    background: black;
}

.movie-info:hover,
.watch-btn:hover{
    background: gold;
    color: black;
    border: none;
    font-weight: 500;
    cursor: pointer;
}
form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}

.search-input {
  border: none;
  border-color: #111;
  border-radius: 5px;
  font-size: 14px;
  height: 30px;
  padding-left: 8px;
  width: 45%;
}

.search-icon {
  border: none;
  margin-left: 10px;
  font-size: 25px;
  background: none;
  color: white;
}

@media (min-width: 1000px) {
  .search-input {
    width: 30%;
  }
}

@media (max-width: 768px) {
  .search-input {
    width: 70%;
  }
}

main {
  margin: 30px 50px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  justify-content: space-between;
  align-items: flex-start;
}

.search-form {
  width: 100%;
}

.movies-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 25px;
  gap: 25px;
}

.movie-details {
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}

.info-box {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background: rgba(0, 0, 0, 0.5);
  height: 100vh;
}

.detail {
  display: flex;
  flex-direction: column;
  grid-gap: 18px;
  grid-gap: 18px;
  gap: 18px;
  padding: 125px 35px;
  align-self: start;
  color: white;
  height: 600px;
  max-height: 1024px;
}

.detail .movie-title {
  font-size: 40px;
}

.original-release,
.running-time {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.row h3 {
  font-size: 20px;
  color: yellow;
}

.row p {
  font-size: 18px;
}

.vote {
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.vote div {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.vote .fa-star {
  font-size: 30px;
  color: yellow;
}

.vote h2 {
  font-size: 30px;
  font-weight: 700;
  margin-left: 10px;
}

.vote span {
  font-size: 20px;
  font-weight: 400;
  margin-right: 10px;
}

.overview {
  font-size: 15px;
  word-spacing: 1.5;
  line-height: 1.5;
  width: 50%;
}

.links {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.links .btn {
  height: 31px;
  font-size: 15px;
  letter-spacing: 1.5px;
  font-weight: 500;
  text-decoration: none;
  border-radius: 20px;
  border: none;
  background: white;
  cursor: pointer;
}

.links .home {
  width: 96px;
  background: yellow;
  line-height: 2;
  display: flex;
  justify-content: center;
  color: black;
}

.links .trailer {
  width: 150px;
}

@media (max-width: 900px) {
  .overview {
    width: 90%;
  }
}

nav {
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 80px;
  background-color: black;
  position: relative;
}

nav a {
  color: white;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
}

.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.fa-bars,
.fa-times {
  display: none;
  color: white;
  font-size: 25px;
  cursor: pointer;
}

.home-link a {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 30px;
}

.nav-links {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.nav-links a,
.nav-links-active a {
  margin-left: 30px;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}

.nav-links a::after {
  content: ' ';
  position: absolute;
  top: 42px;
  left: 0;
  width: 100%;
  height: 2px;
  background: yellow;
  display: none;
}

.nav-links a:hover::after {
  display: block;
  transition: all 0.5s;
}

@media (max-width: 768px) {
  .logo {
    width: 100%;
    padding: 0 50px;
  }

  .fa-bars,
  .fa-times {
    display: block;
  }
  .nav-links {
    display: none;
  }

  .nav-links-active {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    width: 100vw;
    top: 70px;
    z-index: 1000;
    background-color: #101010;
    transition: all 0.5s;
  }

  .nav-links-active a {
    padding: 30px 0;
  }
}

